body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Arial", "Georgia", sans-serif; }

@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  src: url("assets/fonts/arial"); }

@font-face {
  font-family: "Georgia";
  font-style: normal;
  font-weight: 400;
  src: url("assets/fonts/georgia"); }

$main-color: #e5e5e5;
$color-light-xl: #ffffff;
$start-screen-gradient-background: radial-gradient(237.5% 111.8% at 0% 0%, rgb(91, 72, 58) 0%, rgb(38, 36, 37) 100%);


.wrapper {
  display: flex;
  flex-direction: column;
  height: 3800px;
  margin: 0 auto; }

.start-screen {
  display: flex;
  justify-content: center;
  min-width: 1280px;
  min-height: 910px;
  background: $start-screen-gradient-background;
  &__container {
    width: 1280px; } }

.content {
  display: flex;
  justify-content: space-between;
  width: 1200px;
  height: 728px;
  padding: 180px 40px 0;
  &__description {
    position: relative;
    width: 460px;
    height: 322px;
    top: 163px;
    &_heading {
      font-weight: 300;
      font-size: 20px;
      line-height: 130%;
      color: $color-light-xl; }
    &_text {
      font-weight: 200;
      font-size: 16px;
      line-height: 130%;
      color: $color-light-xl; } }
  &__puppy_photo {
      width: 698px;
      height: 728px;
      right: 0;
      bottom: 0; } }



